.header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 30px;
  
    &__text{
      
      padding-left: 5px;
      font-family: $poppins;
      font-size: 11px;
      font-weight: 300;
      line-height: normal;      
      color: $white-50;  
      display: inline-block;
        @media screen and (min-width: 768px) {
            font-size: 14px;
          }
  }

    &__title {
      font-family: $montserrat;
      font-size: 16px;
      font-weight: bold;
      line-height: normal; 
      color: $white;
      @media screen and (min-width: 768px) {
      font-size: 40px;
       }
  }

    &__text2{
      font-family:$montserrat;
      font-size: 9px;      
      font-weight:lighter;  
      padding-bottom: 1px;
      text-align: center;
      color:$white;
      @media screen and (min-width: 768px) {
          font-size: 18px;
        }
    }

    &__button {
      background-color:$dark-lilac;
      color: $white;
      border: none;
      border-radius: 20px;
      width: 90px;
      height: 17px;  
      font-size: 8px;     
      font-family: $roboto;
      @media screen and (min-width: 768px) {
        width: 154px;
        height: 29px;
        font-size: 15px;
        }
  }
    &__container {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding-bottom: 30px;
      
    }

    &__logo{
      height: 35px;
      width: 112px;
    }

    &__container2 {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      gap: 10px;  
    }
}

.fa-sharp {
  font-size: 23px;
  color: $white;
}
